*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  font-size: 62.5%;
  box-sizing: border-box; }
  @media only screen and (max-width: 37.5em) {
    html {
      font-size: 45%; } }

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

@font-face {
  font-family: 'SEGA LOGO FONT';
  font-style: normal;
  font-weight: normal;
  src: local("SEGA LOGO FONT"), url("../css/SEGA.woff") format("woff"); }

.btn-round {
  display: block;
  padding: 2rem;
  border-radius: 10px;
  text-transform: uppercase;
  background-color: #282434;
  border: none;
  font-size: 1.6rem;
  color: #eee;
  position: relative;
  transition: all .2s; }
  .btn-round:active, .btn-round:focus {
    outline: none; }
  .btn-round::before, .btn-round::after {
    content: "";
    position: absolute;
    border-radius: 10px;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0; }
  .btn-round::before {
    border: 2px solid #282434;
    animation: cool-border 2s infinite; }
  .btn-round::after {
    border: 2px solid black;
    animation: cool-border 2.2s infinite; }
  .btn-round:hover {
    background-color: black;
    transform: scale(1.2); }
  .btn-round:active {
    transform: scale(1.1); }

@keyframes cool-border {
  0% {
    transform: scale(1);
    opacity: 1; }
  100% {
    transform: scale(1.5);
    opacity: 0; } }

.btn-simple {
  width: 50%;
  padding: 1rem;
  font-size: 4rem;
  text-transform: uppercase; }

.head {
  font-family: 'SEGA LOGO FONT';
  display: block;
  font-size: 2rem;
  width: 30rem;
  margin: 2rem auto;
  letter-spacing: .5rem; }

.space {
  font-family: 'Courier New';
  width: 95vw;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 2rem;
  grid-gap: 2rem;
  margin: 0 auto; }
  .space__header {
    font-size: 3rem;
    grid-column: 1/-1; }
  .space__para {
    font-size: 1.6rem;
    grid-column: 1/-1; }
  .space__img-box {
    width: 20rem;
    height: 20rem;
    border: 1px solid black;
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-item: center;
    justify-self: center; }
  .space__img {
    width: 25rem; }
  .space__result {
    font-size: 2rem;
    font-weight: 400;
    align-self: center; }
    .space__result p:not(:last-child) {
      margin-top: 2.2rem; }

.story {
  width: 95vw;
  border-top: 1px dashed black;
  margin: 4rem auto;
  padding: 4rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  text-align: center; }
  .story__heading {
    font-size: 2rem;
    margin: 1rem 0; }
  .story__para {
    font-size: 1.6rem;
    margin-bottom: 3rem; }
  .story__input {
    width: 60%;
    margin-bottom: 1rem; }

.fact {
  font-size: 1.6rem;
  font-style: italic;
  margin: 2rem; }
